import $ from 'admin-lte/plugins/jquery/jquery.min.js';
import { showLoading, hideLoading } from './app.loading';


// Initialise les gestionnaires d'vnements
export function setupSearchHandler() {
    $(document).ready(() => {


        // Traiter seulement les page : CRUD
        // Check if the element with the ID "crud_search_input" exists
        if ($("#crud_search_input").length === 0) {
            // If it doesn't exist, return early and do nothing
            return;
        }

        // Initialisation des paramtres  partir de l'URL
        const searchValue = getUrlParameter("searchValue");
        const page = getUrlParameter("page") || 1;

        if (searchValue) $("#crud_search_input").val(searchValue);

        // Gestion de la pagination
        $("body").on("click", ".pagination .page-link", (event) => {
            event.preventDefault();
            // Rcuprer le numro de page
            const page = $(event.target).data("page") || $(event.target).attr("data-page") || $(event.target).text().trim();


            const searchValue = $("#crud_search_input").val();
            fetchData(page, searchValue);
        });

        // Ajout d'un dlai pour limiter les requtes envoyes au serveur
        let debounceTimeout;

        $("body").on("keyup", "#crud_search_input", () => {
            const searchValue = $("#crud_search_input").val();

            clearTimeout(debounceTimeout); // Annule le dlai prcdent
            debounceTimeout = setTimeout(() => {
                if (searchValue === "") {
                    updateURLParameter("searchValue", undefined); // Rinitialise l'URL
                    fetchData(1, ""); // Charge les donnes par dfaut
                } else {
                    fetchData(1, searchValue); // Charge les donnes filtres
                }
            }, 500); // Dlais de 500ms avant la requte
        });


        // Gestion de l'import
        $(document).on("change", "#upload", () => {
            $("#importForm").submit();
        });

        // // Initialisation des dropdowns
        // $(".dropdown-toggle").dropdown();
    });
}


// Met  jour un paramtre dans l'URL
function updateURLParameter(param, value) {
    const url = new URL(window.location.href);
    if (value === undefined || value === null || value === "" ) {
        url.searchParams.delete(param);
    } else {
        url.searchParams.set(param, value);
    }
    window.history.replaceState({}, "", url);
}

// Rcupre un paramtre depuis l'URL
function getUrlParameter(name) {
    return new URLSearchParams(window.location.search).get(name) || "";
}

// Effectue une requte AJAX pour rcuprer des donnes
function fetchData(page = 1, searchValue = "") {
    const url = `${window.location.pathname}/?page=${page}&searchValue=${searchValue}`;

    if (showLoading()) {
        setTimeout(makeRequest, 300); // Ajoute un dlai si ncessaire
    } else {
        makeRequest();
    }

    function makeRequest() {
        $.ajax({
            url,
            method: "GET",
            success(response) {
                $('#data-container').html(response.html);
                hideLoading();
            },
            error(xhr, status, errorThrown) {
                console.error("Statut de l'erreur:", status);
                console.error("Message d'erreur:", errorThrown);
                // console.error("Dtails de la rponse:", xhr.responseText);
             
                // Affichage d'un message d'erreur convivial
                let v = xhr.responseJSON;
                alert(v["message"]);
            
                // Vous pouvez galement afficher l'erreur dans un lment ddi, comme un message au-dessus du tableau.
                $("#error-message").text("Une erreur s'est produite. Veuillez vrifier votre connexion ou ressayer plus tard.");
            },
        });

        // Met  jour l'URL avec les nouveaux paramtres
        updateURLParameter("page", page);
        updateURLParameter("searchValue", searchValue);
    }
}

